<script>
    import { createNamespacedHelpers } from "vuex";
    const { mapState } = createNamespacedHelpers("findModule");

    export default {
        name:'find',
        data() {
            return {
              value: '',
              activeName:''
            };
        },

        computed:{
            ...mapState(['detailDataId','findData'])
        },

        created() {
            this.axios({
                method: 'GET',
                url: 'https://douban.uieee.com/v2/movie/coming_soon'
            }).then(result => {
                // console.log('result.data.subjects ==> ', result.data.subjects);
                this.$store.commit('findModule/getDetailData',result.data.subjects);

                for(let i = 0 ,len = this.detailDataId.length;i<len; i++){
                    this.getFindData(this.detailDataId[i]);
                }
            }).catch(err => {
                // console.log('err ==> ', err);
            })
             
        },

        methods:{
            // 用于样式书写即将上映魔镜奇缘3 id ：30482829
            // 详情
            getFindData(id){
                // 加载提示
                this.$toast.loading({
                  duration:0,
                  message:'数据加载中....'
                });
                this.axios({
                    method:'GET',
                    url: 'https://douban.uieee.com/v2/movie/subject/' + id
                }).then(result =>{
                    // console.log('DetailData result=>',result);
                    this.$store.commit('findModule/getFindDataData',result);
                     this.$toast.clear();
                   
                }).catch( err=>{
                    // console.log('err =>',err);
                     this.$toast.clear();
                })

            },

        },

    }
</script>

